<!--  -->
<template>
<div class="star" :class= "starType">
    <span v-for= "itemClass in itemClasses"  :class= "itemClass" class= "start-item"></span>
</div>
</template>

<script>
    const LENGTH =5;
    const CLS_ON ='on';
    const CLS_HALF ='half';
    const CLS_OFF ='off';
    export default{
        props: {
            size:{
                type:Number
            },
            score: {
                type:Number
            }
        },
        //vue的计算属性方法
        computed:{
            starType(){
                return 'star-'+this.size
            },
            itemClasses(){
                let result =[];
                let score=Math.floor(this.score*2)/2;
                let hasDecimal= score %1 !==0;
                let integer=Math.floor(score);
                for(let i=0;i<integer;i++){
                    result.push(CLS_ON);
                }
                if(hasDecimal){
                    result.push(CLS_HALF);
                }
                while (result.length<LENGTH){
                    result.push(CLS_OFF);
                }
                return result
            }
        }
    }
</script>
<style lang='stylus' rel="stylesheet/stylus">
    @import '../../common/stylus/mixin'
    .star
        font-size :0
        .start-item
            display: inline-block
            background-repeat :no-repeat
        &.star-48
            .start-item
                width:20px
                height :20px
                margin-right :1.375rem /* 22/16 */
                background-size :20px 1.25rem /* 20/16 */
                &:last-child
                    margin-right :0
                &.on
                    bg-image('img/star48_on')
                &.half
                    bg-image('img/star48_half')
                &.off
                    bg-image('img/star48_off')
        &.star-36
            .start-item
                width:15px
                height :15px
                margin-right :6px
                background-size :15px 15px /* 20/16 */
                &:last-child
                    margin-right :0
                &.on
                    bg-image('img/star36_on')
                &.half
                    bg-image('img/star36_half')
                &.off
                    bg-image('img/star36_off')
        &.star-24
            .start-item
                width:10px
                height :10px
                margin-right :3px
                background-size :10px 10px /* 20/16 */
                &:last-child
                    margin-right :0
                &.on
                    bg-image('img/star24_on')
                &.half
                    bg-image('img/star24_half')
                &.off
                    bg-image('img/star24_off')
</style>


